<template>
  <div id="app">  
    <a @click="testModal" class="waves-effect waves-light btn" href="#modal1"><i class="material-icons">done</i></a> 
    <h3 v-if="currentUser != ''">Welcome {{ currentUser }}</h3> 
    <loader v-else-if="!isLoading" size="small" centered="true"></loader>
    <pagination @pageclick="Page($event)" @nextclick="NextPage()" @prevclick="PrevPage()" :all-disable="!canClick" :count="totalItems" :selected="pageSelected" :items-per-page="itemsPerPage"></pagination>
    <table v-if="!isLoading" class="bordered highlight">
		  <thead>
			  <tr>
          <th scope="col"></th>
          <th scope="col">Created</th>
          <th scope="col">Created By</th>
          <th scope="col">Status</th>
			</tr>
		</thead>
      <tbody>
        <row @modalapprove="ModalApprove" @modalreject="ModalReject" v-for="item in data" :user="currentUser" :key="item.Created" :item="item"></row>
      </tbody>
    </table>
    <loader v-else size="big" centered="true"></loader>
    <pagination @pageclick="Page($event)" @nextclick="NextPage()" @prevclick="PrevPage()" :all-disable="!canClick" :count="totalItems" :selected="pageSelected" :items-per-page="itemsPerPage"></pagination>

    <modal ref="modal1" :buttons="modalButtons" :error-message="modalError" @buttonclick="ModalClick">
      <template slot="header">
        {{modalHeader}}
      </template>  
      <template slot="body">
        {{modalText}}
        <modal-table v-if="modalTable != null" :rows="modalTable"></modal-table>
      </template>    
      </modal>
  </div>
</template>

<script>
import $ from "jquery";
import loader from "./components/loader.vue";
import extensions from "./extensions";
import provider from "./providers/restProvider";
import responser from "./responser";
import modal from "./components/modal.vue";
import row from "./components/row.vue";
import modalTable from "./components/modalTable.vue";
import pagination from "./components/pagination.vue";
import moment from "moment";

export default {
  name: "app",
  components: {
    loader,
    modal,
    row,
    modalTable,
    pagination
  },
  data() {
    return {
      data: [],
      isLoading: true,
      currentUser: "",
      canClick: false,
      modalHeader: "",
      modalText: "",
      modalButtons: [],
      modalTable: false,
      modalError: "something",
      currentItem: null,
      totalItems: null,
      itemsPerPage: 4,
      pageSelected: 1,
      paginationString: "",
      num: require("numeral")
    };
  },
  mounted: function() {
    extensions.registerExtensions();
    this.GetData();
  },
  methods: {
    NextPage: function() {
      this.pageSelected++;
      var last = this.data[this.data.length - 1];
      this.GetPageOnlyData(false);
    },
    PrevPage: function() {
      this.pageSelected--;
      var last = Date.parse(this.data[0].Created);
      console.dir(this.data[0].Created);
      console.dir(last);
      var d =
        last.getYear() +
        last.getDay() +
        last.getMonth() +
        "%20" +
        lash.getFullHour() +
        "%3a" +
        last.getMinute() +
        "%3a" +
        last.getSecond();
      this.paginationString = "Paged=TRUE&p_Created=" + d;
      this.GetPageOnlyData(true);
    },
    Page: function(num) {
      this.pageSelected = num;
    },
    testModal: function() {
      this.PrepareModal("Test", "Some text", [{ text: "OK" }], null);
      var v = this.$refs.modal1.ShowModal();
      console.log(v);
      v.then(function(p) {
        console.log(p);
      });
    },
    PostProcess: function(data, approve) {
      if (approve === undefined) approve = true;
      var err;
      if (approve == true) {
        err = responser.PostApprove(this.currentItem, data, this.currentUser);
      } else {
        err = responser.PostReject(this.currentItem, data, this.currentUser);
      }
      if (err) {
        this.PrepareModal(
          "Error occurred.",
          "There is some error",
          [{ text: "OK" }],
          null,
          data
        );
        this.$refs.modal1.ShowModal();
      }
    },
    ModalClick: function(button) {
      var self = this;
      console.dir(button);
      if (button == "Approve") {
        var res = provider.GetData(
          "/Response/Approve",
          "guid=" + this.currentItem.RequestID
        );
        this.currentItem.Status = "Processing...";
        res.done(function(data) {
          console.dir(data);
          self.PostProcess(data);
        });
      } else if (button == "Reject") {
        var res = provider.GetData(
          "/Response/Reject",
          "guid=" + this.currentItem.RequestID
        );
        this.currentItem.Status = "Processing...";
        res.done(function(data) {
          console.dir(data);
          self.PostProcess(data, false);
        });
      }
    },
    ModalApprove: function(item) {
      var self = this;
      this.currentItem = item;
      this.PrepareModal(
        "Confirm approval",
        "Are you sure you want to approve following items?",
        [{ text: "Approve" }, { text: "Cancel" }],
        item.Items,
        null
      );
    },
    ModalReject: function(item) {
      this.currentItem = item;
      this.PrepareModal(
        "Confirm reject",
        "Are you sure you want to reject this request?",
        [{ text: "Reject" }, { text: "Cancel" }],
        null,
        null
      );
    },
    GetPageOnlyData: function(prev) {
      this.isLoading = true;
      this.canClick = false;
      var self = this;

      var getDataResult = provider.GetData(
        null,
        "count=" +
          this.itemsPerPage +
          "&position=" +
          self.paginationString.replace("&", "_AMP_") +
          (prev ? "&prev=true" : "")
      );
      console.dir(self.paginationString);
      getDataResult
        .done(function(data) {
          if (data) {
            self.data = data.Data;
            if (data.Pos != null) self.paginationString = data.Pos;
          }
          self.canClick = true;
          self.isLoading = false;
        })
        .fail(function(response) {
          self.isLoading = false;
          self.canClick = false;
          console.dir(response);
          self.PrepareModal(
            response.statusText,
            null,
            [{ text: "Well, OK then..." }],
            null,
            response.responseText
          );
          self.$refs.modal1.ShowModal();
        });
    },
    GetData: function() {
      this.isLoading = true;
      this.canClick = false;
      var self = this;

      var getDataResult = provider.GetData(null, "count=" + this.itemsPerPage);
      var userResult = provider.GetData("/RequestsOverview/GetCurrentUser");
      var totalItems = provider.GetData("/RequestsOverview/GetItemCount");
      var isAuthenticated = provider.GetData(
        "/RequestsOverview/IsAuthenticated"
      );

      isAuthenticated
        .done(function(response) {
          $.when(getDataResult, userResult, totalItems)
            .done(function(data, user, count) {
              if (data) {
                self.data = data.Data;
                self.paginationString = data.Pos;
              }
              if (user) {
                self.currentUser = user;
              }
              if (count) {
                self.totalItems = count;
                console.dir(self.totalItems);
              }
              self.canClick = true;
              self.isLoading = false;
            })
            .fail(function(response) {
              self.isLoading = false;
              self.canClick = false;
              console.dir(response);
              self.PrepareModal(
                response.statusText,
                null,
                [{ text: "Well, OK then..." }],
                null,
                response.responseText
              );
              self.$refs.modal1.ShowModal();
            });
        })
        .fail(function(response) {
          if (response.status == 401) window.location.replace("/Authenticate");
          else {
            self.isLoading = false;
            self.canClick = false;
            console.dir(response);
            self.PrepareModal(
              response.statusText,
              null,
              [{ text: "Well, OK then..." }],
              null,
              response.responseText
            );
            self.$refs.modal1.ShowModal();
          }
        });
    },
    PrepareModal: function(header, content, buttons, table, error) {
      this.modalHeader = header;
      this.modalText = content;
      this.modalButtons = buttons;
      this.modalTable = table;
      this.modalError = error;
    }
  }
};
</script>

<style>
#app {
  margin-top: 50px;
}
</style>



