<template>  
  <ul class="pagination right-align">
    <li :class="(selected == 1 || allDisable) ? 'disabled' : 'waves-effect'"><a @click="PrevClick()" href="#!"><i class="material-icons">chevron_left</i></a></li>
    <li v-for="num in GetNumOfPages()" @click="PageClick(num)" :key="num" :class="BindClass(num)"><a href="#!">{{ num }}</a></li>
    <!-- <li class="waves-effect"><a href="#!">2</a></li>
    <li class="waves-effect"><a href="#!">3</a></li>
    <li class="waves-effect"><a href="#!">4</a></li>
    <li class="waves-effect"><a href="#!">5</a></li> -->
    <li :class="(selected == GetNumOfPages() || allDisable) ? 'disabled' : 'waves-effect'"><a @click="NextClick()" href="#!"><i class="material-icons">chevron_right</i></a></li>
  </ul>
            
</template>

<script>
export default {
  name: "pagination",
  props: ["count", "selected", "itemsPerPage", "allDisable"],
  methods: {
    GetNumOfPages: function() {
      var pages = this.count / this.itemsPerPage;
      return Math.floor(pages) + 1;
    },
    PrevClick: function() {
      if (this.selected == 1 || this.allDisable) return;
      this.$emit("prevclick");
    },
    NextClick: function() {
      if (this.selected == this.GetNumOfPages() || this.allDisable) return;
      this.$emit("nextclick");
    },
    PageClick: function(num) {
      if (this.allDisable) return;
      this.$emit("pageclick", num);
    },
    BindClass: function(num) {
      var c = "";
      if (this.allDisable) c = "disabled ";
      c += this.selected == num ? "active" : "waves-effect";
      return c;
    }
  }
};
</script>

<style>

</style>



